<template>
  <div class="d-flex flex-column">
    <div v-for="nationGroup in nations">
      <div class="d-flex justify-content-between">
        <div v-for="nation in nationGroup">
          <b-card :title="nation.name" class="nationCard" v-on:click="startWar(nation.id)">
            <p>{{ nation.custom }}</p>
            <p>{{ nation.minBaseVal }} - {{ nation.maxBaseVal }}</p>
          </b-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Nation from '../lib/Nation';

  export default {
    computed: {
      nations() {
        const allNations = Nation.allNations();
        const nations = [];
        while (allNations.length > 0) {
          nations.push(allNations.splice(0, 3))
        }
        return nations;
      }
    },
    methods: {
      startWar: function (nationId) {
        console.log('start war with '+nationId)
      }
    }
  }
</script>

<style>
.nationCard {
  min-width: 15rem;
  margin-bottom: 10px;
  margin-top: 10px;
}
</style>



